@import "../../../styles/mixins";

.news {
	z-index: 10;
	margin: 40px auto 0 auto;

	@include media(sm) {
		margin-top: 30px;
	}
	@include media(md) {
		margin-top: 60px;
	}
	@include media(lg) {
		margin-top: 280px;
	}

	&__container {
		@include section();
	}

	&__title {
		font-family: 'Kazimir Display', sans-serif;
		
		font-weight: bold;
		line-height: 1.26;
		color: #ffffff;
		margin-top: 10px;
		font-size: 36px;

		@include media(sm) {
			font-size: 42px;
		}
		@include media(md) {
			font-size: 54px;
		}
	}
}

.catalog {
	min-height: 100vh;
	margin: 0 auto;

	&__container {
		@include section();
	}

	&__title {
		font-family: 'Kazimir Display', sans-serif;
		font-weight: bold;
		line-height: 1.26;
		color: #313131;
		margin-top: 10px;
		font-size: 36px;
		@include media(sm) {
			font-size: 42px;
		}
		@include media(md) {
			font-size: 54px;
		}
	}

	&__filters {
		justify-content: stretch;
		height: 80px;
		box-shadow: 0 12px 60px 14px rgba(174, 172, 205, 0.09);;
		list-style-type: none;
		padding: 0;
		margin-top: 30px;
		display: none;

		@include media(sm) {
		}
		@include media(md) {
		}
		@include media(lg) {
			display: flex;
		}
	}

	&__filter {
		display: flex;
		flex-grow: 1;
		justify-content: center;

		&:not(:nth-child(1)) {
		  border-left: 1px solid #F3F3F3;
		}
	}

	&__filterPrice {
	  display: flex;
		align-items: center;
		justify-content: center;
		padding-right: 30px;
		height: 100%
	}

	&__openMobileFilters {
		display: block;
		font-size: 12px;
		width: 240px;
		margin: 0 auto;
		text-align: center;

		@include media(xs) {

		}
		@include media(sm) {
			display: none;
		}
		@include media(md) {

		}
		@include media(lg) {

		}
	}
}

.catalogLoader {
	margin: 40px 0;
}

.mobileFiltersPanel {
	display: inline-flex;
	justify-content: start;
	box-shadow: 0 0 30px -4px rgba(0, 0, 0, 0.31);
	margin: 10px 0 20px 0;

	@include media(xs) {

	}
	@include media(sm) {
	}
	@include media(md) {
	}
	@include media(lg) {
		display: none;
	}

	& &__button {
	  background-color: transparent;
		outline: none;
		border: none;
		font-size: 14px;
		font-family: Roboto, sans-serif;

		&_theme {
		  &_open {
		    padding: 0 20px;
		  }

			&_reset {
				border-left: 1px solid #F3F3F3;

				.iconText__text {
					font-weight: normal;
					font-size: 14px;
					font-family: Roboto, sans-serif;
				}
			}
		}
	}
}

.transformCheckboxMobile {
	padding: 0 20px;
	margin-top: 10px;
	//border-bottom: 1px solid #F3F3F3;
}

.mobileFilterPrice {
	margin-top: 8px;
}

.modalContent {
	.mobileFiltersSubmit {
		display: block;
		margin: 20px auto 0 auto;
		font-size: 14px;
		font-weight: 400;
		padding-left: 16px;
		padding-right: 16px;
		width: 280px;
		text-align: center;
	}
}