@import "../mixins";

.headerBasket_openMobile {
	&.headerBasket__panelWrap {
		visibility: visible;
	}

	.headerBasket__panel {
		padding: 0;
		box-shadow: none;
		background-color: transparent;
	}
}

.headerBasket_openMobile.headerBasket_withAnimateItems {
	.headerBasket__product:nth-child(even) {
		animation: appearBasketItemOpacityRTL 400ms ease-out 200ms;
		animation-fill-mode: forwards;
	}

	.headerBasket__product:nth-child(odd) {
		animation: appearBasketItemOpacityLTR 400ms ease-out 200ms;
		animation-fill-mode: forwards;
	}
}

.headerBasket_open.headerBasket_withAnimateItems {
	.headerBasket__product:nth-child(even) {
		animation: appearBasketItemOpacityRTL 400ms ease-out 200ms;
		animation-fill-mode: forwards;
	}

	.headerBasket__product:nth-child(odd) {
		animation: appearBasketItemOpacityLTR 400ms ease-out 200ms;
		animation-fill-mode: forwards;
	}
}

.headerBasket_withAnimateItems {
	.headerBasket__product {
		opacity: 0;
	}
}

.headerBasket {
	$root: &;

	height: 100%;
	border-left: 1px solid rgba(22, 22, 22, 0.08);

	&__button {
		height: 100%;
		background-color: #fff;
		border: none;
		cursor: pointer;
		outline: none;
		transition: background-color 200ms ease-out;
		position: relative;
		padding: 0 15px 0 15px;
		@include media(sm) {
			padding: 0 30px 0 30px;
		}

		&:hover {
			.basket__icon {
				animation: basketIconHover 200ms 2 ease-out;
			}

			.basket__circle_active {
				animation: basketCircleHover 200ms 2 ease-out 300ms;
			}
		}
	}

	&_open {

		#{$root}__button {
			background-color: #313D53;
			position: relative;
			&:after {
				content: '';
				position: absolute;
				border-width: 8px;
				border-style: solid;
				border-color: transparent transparent #FFF transparent;
				left: 25px;
				bottom: 0;
				display: none;

				@include media(xs) {

				}
				@include media(sm) {
					left: 40px;
					display: block;
				}
				@include media(md) {

				}
				@include media(lg) {

				}
			}
		}

		.iconText__text {
			color: #fff;
		}

		.basket {
			background-color: #fff;

			path {
				fill: #313D53;
			}
		}

		#{$root}__panelWrap {
			animation: appearBasketPanel 200ms ease-out;
			animation-fill-mode: forwards;
		}
	}

	&_close {
		#{$root}__panelWrap {
			animation: hideBasketPanel 200ms ease-out;
			animation-fill-mode: forwards;
		}
	}

	&__panelWrap {
		padding: 0 10px;
		//position: absolute;
		//top: 100%;
		//right: 0;
		//left: 0;
		visibility: hidden;
		display: none;

	  @include media(xs) {

	  }
	  @include media(sm) {
		  display: block;
		  position: absolute;
		  right: 0;
		  top: 100%;
			width: 555px;
		  padding: 0;
	  }
	  @include media(md) {

	  }
	  @include media(lg) {

	  }
	}
	
	&__panel {
		max-width: 100%;
		padding: 40px 50px;
		background-color: #fff;
		border-bottom-left-radius: 6px;
		border-bottom-right-radius: 6px;
		box-shadow: 0 15px 80px -10px rgba(66, 66, 66, 0.5);
		z-index: 100;
		width: 100%;

		@include media(xs) {
		}
		@include media(sm) {
		}
		@include media(md) {
		}
		@include media(lg) {
		}
	}

	&__title {
		font-family: 'Kazimir Display', serif;
		font-size: 28px;
		font-weight: bold;
		line-height: 1.57;
		color: #313131;
		text-align: left;
		border-bottom: 2px solid #bdb4f0;
		padding-bottom: 25px;
	}

	&__product {
		border-bottom: 2px solid #EDEDED;
		padding: 20px 0;
		display: flex;
		text-align: left;
		position: relative;
	}

	&__wrapImage {
		display: flex;
		align-items: center;
		justify-content: center;
		width: 120px;
		height: 120px;
		overflow: hidden;
		border-radius: 5px;
	}

	&__image {
		width: 100%;
	}

	&__wrapInfo {
		padding: 16px 20px;
		width: 100%;
	}

	&__productTitle {
		font-family: 'Gotham Pro', sans-serif;
		font-size: 16px;
		font-weight: bold;
		line-height: 1.5;
		color: #000000;
	}

	&__description {
		margin-top: 10px;
		font-family: 'Gotham Pro', sans-serif;
		font-size: 14px;
		font-weight: 300;
		line-height: 1.71;
		color: #777777;
	}

	&__price {
		font-family: 'Gotham Pro', sans-serif;
		font-size: 20px;
		font-weight: 500;
		line-height: 1.2;
		color: #000000;
	}

	&__productBottomGroup {
		display: flex;
		align-items: center;
		justify-content: space-between;
		padding-top: 10px;
	}

	&__counter {
		display: flex;
		align-items: center;
	}

	&__counterButton {
		outline: none;
		font-family: 'Kazimir Text', sans-serif;
		font-weight: 500;
		background-color: transparent;
		border: 1px solid #BDB4F0;
		border-radius: 50%;
		height: 25px;
		width: 25px;
		display: flex;
		font-size: 16px;
		padding: 0;
		cursor: pointer;
		align-items: center;
		justify-content: center;
	}

	&__counterValue {
		padding: 0 10px;
		font-family: 'Gotham Pro', sans-serif;
		font-size: 16px;
		font-weight: 500;
		color: #000000;
	}

	&__iconText_theme_remove {
		cursor: pointer;

		&:hover {
			.iconText__text {
				text-decoration: underline;
			}
		}

		.basket {
			background-color: #989EA9;

			path {
				fill: #fff;
			}
		}

		.iconText__text {
			font-family: 'Gotham Pro', sans-serif;
			font-size: 14px;
			line-height: 1.86;
			color: #000000;
		}
	}

	&__rowSum {
	  display: flex;
		justify-content: space-between;
		padding: 30px 0;
	}

	&__sumLabel {
		font-family: 'Gotham Pro', sans-serif;
		font-size: 24px;
		line-height: 1;
		color: #000000;
	}

	&__sumValue {
		font-family: 'Gotham Pro', sans-serif;
		font-size: 24px;
		font-weight: bold;
		line-height: 1;
		color: #000000;
	}

	&__footerButton {
		width: 100%;
		display: flex;
		justify-content: center;
	}
}

.containerReturn{
	display: none;
	position: absolute;
	top: 0;
	left: 0;
	height: 100%;
	width: 100%;
	background: rgba(66, 66, 66, 0.9);
}

.innerContainerReturn {
	height: 100%;
	width: 100%;
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	align-items: center;
	padding: 20px 0;
}

.containerReturn__question{
	color: white;
	font-size: 22px;
	font-family: 'Gotham Pro', sans-serif;
	text-align: center;
	flex-basis: 100%;
}

.containerReturn__buttonAgree,
.containerReturn__buttonCancel {
	height: 42px;
	line-height: 42px;
	padding: 0 20px;
	color: #fff;
	font-family: 'Gotham Pro', sans-serif;
	font-size: 13px;
	text-transform: uppercase;
	border-radius: 30px;
	margin: 0 5px;
	cursor: pointer;
	letter-spacing: 1px;
	transition: background-color 200ms ease-out, box-shadow 200ms ease-out, text-shadow 100ms ease-out, -webkit-box-shadow 200ms ease-out;
}

.containerReturn__buttonCancel{
	background-color: #6F68FC;
	&:hover{
		background-color: #5c56d0;
	}
}

.containerReturn__buttonAgree{
	background-color: #ce5468;
	&:hover{
		background-color: #ad3f51;
	}
}

@media only screen and (max-width: 580px) {
	.headerBasket{
		&__panel{
			padding: 30px;
		}
		&__wrapImage{
			flex-shrink: 0;
			height: auto;
		}
		&__productBottomGroup{
			flex-wrap: wrap;
			justify-content: flex-start;
		}
		&__price{
			width: 100px;
		}
		&__iconText_theme_remove{
			flex-basis: 100%;
			margin-top: 15px;
		}
	}
}
@media only screen and (max-width: 415px){
	.headerBasket{
		&__product{
			flex-wrap: wrap;
		}
		&__wrapImage{
			width: 100%;
    	height: 160px;
		}
		&__wrapInfo{
			padding-bottom: 0;
		}
		&__iconText_theme_remove{
			margin-top: 20px;
		}
		&__panel{
			padding: 15px 15px 30px 15px;
		}
		&__title{
			padding-bottom: 15px;
		}
		.button_theme_primary{
			font-size: 14px;
		}
	}
}

@keyframes appearBasketPanel {
	from {
		opacity: 0;
		visibility: visible;
	}
	to {
		opacity: 1;
		visibility: visible;
	}
}

@keyframes hideBasketPanel {
	from {
		opacity: 1;
		visibility: visible;
	}
	to {
		opacity: 0;
		visibility: hidden;
	}
}

@keyframes appearBasketItemOpacityLTR {
	from {
		transform: translateX(-50px);
		opacity: 0;
	}
	to {
		transform: translateX(0);
		opacity: 1;
	}
}

@keyframes appearBasketItemOpacityRTL {
	from {
		transform: translateX(50px);
		opacity: 0;
	}
	to {
		transform: translateX(0);
		opacity: 1;
	}
}

@mixin rotate($name, $deg) {
	@keyframes #{$name} {
		0% {
			transform: rotate(0);
		}
		33.3333% {
			transform: rotate(#{$deg}deg);
		}
		66.6666% {
			transform: rotate(-#{$deg}deg);
		}
		100% {
			transform: rotate(0);
		}
	}
}

@mixin scale($name) {
	@keyframes #{$name} {
		0% {
			transform: scale(1);
		}
		50% {
			transform: scale(0.75);
		}
		100% {
			transform: scale(1);
		}
	}
}

@include rotate(basketIconHover, 8);
@include scale(basketCircleHover);
