.accordion {
	width: 100%;

	&__button {
		display: flex;
		align-items: center;
		background-color: transparent;
		border: none;
		width: 100%;
		padding-right: 20px;
		padding-left: 20px;
		text-align: left;
		border-bottom: 1px solid #d7d7d7;
		font-family: Roboto, sans-serif;
		font-size: 14px;
		height: 40px;
	}

	&__label {
	  flex-grow: 1;
	}

	&__icon {
		transition: transform 200ms ease-out;
	}

	&__contentWrap {
	  display: none;
	}

	&__content {
		padding: 5px 25px 5px 25px;
		box-shadow: 0 0 4px 2px rgba(0, 0, 0, 0.10) inset;
	}
}