@import "../mixins";

@mixin activeLine() {
	content: '';
	height: 3px;
	background-color: #bdb4f0;
	border-top-left-radius: 2px;
	border-top-right-radius: 2px;
	position: absolute;
	left: 0;
	right: 0;
	bottom: -1px;
}

.tabs {
	display: flex;
	list-style-type: none;
	padding: 0;
	overflow-x: auto;
	
	&__tab {
	  padding: 10px 20px;
		border-bottom: 1px solid #EDEDED;
		position: relative;
		cursor: pointer;
		font-family: 'Gotham Pro', sans-serif;
		transition: color 200ms ease-out, transform 200ms ease-out;
		white-space: nowrap;
		font-size: 16px;
		@include media(sm) {
			font-size: 18px;
		}
		@include media(md) {
			font-size: 24px;
		}

		.char {
			transition: color 150ms ease-out calc(40ms * var(--char-index)), 150ms ease-out calc(40ms * var(--char-index));
			color: rgba(0, 0, 0, 0.21);
			transform: scale(0.8);
		}

		&:before {
		  @include activeLine();
			transition: opacity 200ms ease-out;
			opacity: 0;
		}

		&:hover {
			.char {
				color: rgb(0, 0, 0);
				transform: scale(1);
			}
		}

		&_active {
			.char {
				color: #000;
				transform: scale(1);
			}

			&:before {
				@include activeLine();
				opacity: 1;
			}
		}
	}
}