.horizontalAngle {
	display: flex;
	align-items: center;
	justify-content: center;
	background-color: #FDFDFE;
	border: 1px solid #EDEDEE;
	cursor: pointer;
	outline: none;
	padding: 0;

	&_theme {
	  &_circle {
		  border-radius: 50%;
		  height: 42px;
		  min-width: 42px;
		  transition: box-shadow 200ms ease-out;

		  &:hover {
			  box-shadow: 0 0 4px 2px rgba(0, 0, 0, 0.1);
		  }
	  }

		&_rectangle {
			height: 30px;
		}
	}

	&_pos_right {
	  transform: rotateY(180deg);
	}
}