@import "../mixins";

.slider {
	$root: &;

	display: flex;
	margin-left: -10px;
	margin-right: -10px;
	position: relative;

	& &__itemWrap {
		padding: 10px;
		width: 25%;
		outline: none;
		cursor: pointer;
		min-height: 360px;
		display: flex;

		&:hover {
			#{$root}__itemInfo {
				transform: translateZ(50px) scale(1);
				opacity: 1;
			}

			#{$root}__itemInfoBackground {
				transform: translateZ(20px) scale(1);
				opacity: 1;
			}
		}

		.hover-in{
			//transition: 150ms ease-out;
		}

		.hover-out{
			//transition: 150ms ease-in;
		}
	}

	&__wrapImage {
	  display: flex;
		align-items: center;
		background-color: #323E54;
		height: 100%;
		border-radius: 10px;
		overflow: hidden;
	}

	&__image {
		width: 100%;
	}

	&__itemInner {
		position: relative;
	}

	&__itemInfoBackground {
		position: absolute;
		top: 15px;
		left: 15px;
		right: 15px;
		bottom: 15px;
		transform: translateZ(20px) scale(0.8);
		background-color: rgba(0, 0, 0, 0.65);
		opacity: 0;
		transition: transform 200ms ease-out, opacity 200ms ease-out;
		border-radius: 10px;
	}

	&__itemInfo {
		position: absolute;
		top: 20px;
		left: 20px;
		right: 20px;
		bottom: 20px;
		transform: translateZ(20px) scale(0.8);
		transition: transform 200ms ease-out, opacity 200ms ease-out;
		display: flex;
		justify-content: center;
		align-items: center;
		opacity: 0;
		color: white;
		padding: 15px;

		#{$root}__button {
			padding: 10px 25px;
			font-size: 10px;
			margin-top: 15px;
		}
	}

	&__itemInfoInner {
	  text-align: center;
	}

	&__itemInfoTitle {
		font-family: 'Gotham Pro', sase-serif;
		text-align: center;
		font-size: 16px;
		font-weight: 500;
		position: relative;
		padding-bottom: 15px;

		&:before {
		  content: '';
			position: absolute;
			top: 100%;
			left: 50%;
			width: 70px;
			transform: translateX(-50%);
			height: 2px;
			background-color: #C3AAEC;
		}
	}

	&__itemInfoDescription {
	  margin-top: 15px;
		display: inline-flex;
		justify-content: center;
		align-items: flex-start;
		text-align: center;
		font-family: 'Gotham Pro', sans-serif;
		font-size: 14px;
	}

	&__itemInfoLabel {

	}

	&__itemInfoValue {
	}

	&__buttons {
		display: flex;
		justify-content: center;
		align-items: center;
		margin: 20px 0;
		@include media(sm) {
			margin: 40px 0;
		}

		&:before {
			content: '';
			height: 1px;
			background-color: #ECEBEC;
			width: 100%;
			margin-right: 40px;
			display: none;
			@include media(sm) {
				display: block;
			}
		}
	}

	&__horizontalAngle {
		&_dir_right {
			margin-left: 15px;
			transform: rotateY(180deg);
		}
	}
}