@import '../../../styles/mixins';

.modalContainer {
	display: none;
	position: fixed;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	height: 100%;
	width: 100%;
	z-index: 200;
	background: rgba(0, 0, 0, 0.50);
	overflow-y: auto;

	&_theme {
	  &_productView {
	    //padding: ;
	  }
	}
}

.modalDialog {
	display: flex;
	justify-content: center;
	align-items: center;
	min-height: 100%;
	width: 100%;
	padding: 10px;

	@include media(xs) {
		padding: 20px;
	}
	@include media(sm) {
		padding: 40px;
	}
	@include media(md) {
		padding: 60px;
	}
	@include media(lg) {
		padding: 100px 0;
	}

	&_theme {
	  &_productView {
			padding: 20px 0;
	  }

		&_basket {
		  padding-top: 40px;
		  padding-bottom: 40px;
		}
	}
}

.modalForm {
	margin-top: 20px;

	input {
		margin-top: 15px;
		padding: 0 30px;
		height: 50px;
		line-height: 50px;
		width: 100%;
		border-radius: 6px;
		border: 1px solid rgba(22, 22, 22, 0.2);
		box-shadow: 0 12px 60px -14px rgba(174, 172, 205, 0.22);
		font-size: 16px;
		font-family: 'Gotham Pro', sans-serif;
		font-weight: 300;
		&::placeholder {
			opacity: 0.5;
		}
	}

	button {
		margin-top: 35px;
		height: 53px;
		line-height: 53px;
		padding: 0 50px;
		border-radius: 26.5px;
		box-shadow: 0 8px 40px -12px rgba(73, 43, 194, 0.4);
		background-color: #7b5bfd;
		color: #fff;
		font-size: 16px;
		font-family: 'Gotham Pro', sans-serif;
		font-weight: 300;
		letter-spacing: 1px;
		text-transform: uppercase;
		cursor: pointer;
	}

	.input__labelText {
		display: none;
	}

	.input__field {
		text-align: left;
	}

}

.scrollHidden {
	overflow-y: hidden;
}

.blockScroll {
	width: 50px;
	height: 50px;
	overflow-y: scroll;
	visibility: hidden;
}

.modalLoader {
	display: none;
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	z-index: 300;
	background: rgba(66, 66, 66, 0.1);

	.loader__icon {
		height: 80px;
		width: 80px;
	}
}

.showLoader {
	display: flex;
}

.modalContent {
	max-width: 900px;
	width: 100%;
	background: #fff;
	border-radius: 6px;
	box-shadow: 0 15px 80px -10px rgba(66, 66, 66, 0.5);
	position: relative;
	padding: 60px 20px;

	@include media(xs) {

	}
	@include media(sm) {
		padding: 80px 160px 100px 160px;
	}
	@include media(md) {

	}
	@include media(lg) {

	}

	.iconClose {
		cursor: pointer;
		position: absolute;
		height: 20px;
		width: 20px;
		right: 16px;
		top: 16px;

		@include media(xs) {

		}
		@include media(sm) {
			height: 24px;
			width: 24px;
			right: 60px;
			top: 60px;
		}
		@include media(md) {

		}
		@include media(lg) {

		}
	}

	&__title {
		margin: 0;
		font-family: 'Kazimir Display', serif;
		font-weight: bold;
		font-size: 48px;
		line-height: 62px;
		color: #313131;
		position: relative;

		&:before {
			content: "";
			display: block;
			position: absolute;
			height: 2px;
			width: 135px;
			left: 0;
			bottom: -40px;
			background: #bdb4f0;
		}

		&_center {
			text-align: center;

			&:before {
				left: 50%;
				transform: translateX(-50%);
			}
		}
	}

	&__text {
		margin: 70px 0 0 0;
		font-size: 18px;
		font-family: 'Gotham Pro', sans-serif;
		line-height: 24px;
		color: #313131;

		&_center {
			text-align: center;
		}
	}

	&_theme {
		&_productView {
			padding: 0;
			max-width: 60%;
			background-color: transparent;

			.iconClose {
				background-color: rgba(0, 0, 0, 0.5);
				border-radius: 10px;
				right: 4px;
				top: 4px;
				//background-color: black;
				z-index: 100;
				height: 40px;
				width: 40px;
				display: flex;
				align-items: center;
				justify-content: center;

				svg {
					width: 24px;
					height: 24px;
				}

				path {
					fill: white;
				}
			}
		}

		&_noHorizontalPadding {
			padding-left: 0;
			padding-right: 0;
		}

		&_mobileFilters {
			padding: 65px 0 25px 0;
			max-width: 500px;

			.iconClose {
				height: 20px;
				width: 20px;
				right: 16px;
				top: 16px;
			}
		}

		&_basket {

		}
	}
}


    
    




