.basket {
	position: relative;
	height: 34px;
	width: 34px;
	background-color: #313D53;
	border-radius: 50%;

	&__circle {
		background-color: #BDB4F0;
		width: 14px;
		height: 14px;
		position: absolute;
		top: -3px;
		right: -3px;
		border-radius: 50%;
		transform: scale(0);
		transition: transform 200ms ease-out;

		&_active {
			transform: scale(1);
		}
	}

	&__icon {

	}

	&__path {
		position: absolute;
	  width: 14px;
		height: 18px;
	}
}