@import "../mixins";

.intro {
	margin: 0 auto;
	padding-top: 45px;
	height: 300px;

	@include section();

	@include media(xs) {
		padding-top: 45px;
		height: 300px;
	}
	@include media(sm) {
		padding-top: 50px;
		height: 450px;
	}
	@include media(md) {
		padding-top: 80px;
		height: 600px;
	}
	@include media(lg) {
		padding-top: 130px;
		height: 650px;
	}

	&__leftGroup {
		//animation: appearanceFromLeft 500ms ease-out 1 both 700ms;
	}

	&__title {
		font-family: 'Kazimir Display', serif;
		font-weight: bold;
		line-height: 1.28;
		position: relative;
		color: transparent;
		font-size: 22px;
		text-align: center;
		padding-left: 0;
		margin: 0 0 20px 0;
		&:before {
			display: none;
		}

		@include media(xs) {
			font-size: 34px;
		}
		@include media(sm) {
			padding-left: 40px;
			text-align: left;
			font-size: 38px;
			margin: 0 0 40px 0;
			&:before {
				display: block;
			}
		}
		@include media(md) {
			font-size: 46px;
		}
		@include media(lg) {
			font-size: 54px;
		}

		&:before {
			content: '';
			position: absolute;
			top: 0;
			bottom: 0;
			left: 0;
			background-color: #BDB4F0;
			border-radius: 6px;
			width: 4px;
			animation: appearVerticalLineFromCenter 300ms ease-out 1 both 1300ms;
		}

		&_animation {
			color: #313131;

			.char {
				opacity: 1;
				animation: appearanceChar 250ms cubic-bezier(0.150, 0.710, 0.160, 1.010) 1 both;
				animation-delay: calc(40ms * var(--char-index));
			}
		}
	}

	&__info {
		font-family: 'Gotham Pro', sans-serif;
		line-height: 1.33;
		color: #313131;
		opacity: 0;
		font-size: 14px;
		text-align: center;
		@include media(xs) {
		}
		@include media(sm) {
			text-align: left;
			font-size: 18px;
			width: 50%;
		}
		@include media(md) {

		}
		@include media(lg) {
			width: 100%;
		}

		&_animation {
			opacity: 1;

			.char {
				opacity: 1;
				animation: appearanceChar 250ms cubic-bezier(0.150, 0.710, 0.160, 1.010) 1 both;
				animation-delay: calc(25ms * var(--char-index));
			}
		}
	}

	&__buttonContainer {
		text-align: center;
		margin-top: 40px;

		@include media(xs) {

		}
		@include media(sm) {
			margin-top: 50px;
			text-align: left;
		}
		@include media(md) {

		}
		@include media(lg) {

		}
	}

	&__button {
		animation: appearanceFromLeft 500ms ease-out 1 both 4000ms;
	}

	&__image {
		position: absolute;
		width: 60%;
		height: auto;
		top: 0;
		right: 0;
		box-shadow: 0 40px 80px -40px rgba(0, 0, 0, 0.2);
		animation: appearanceFromRight 500ms ease-out 1 both 700ms;
		display: none;
		@include media(sm) {
			display: block;
		}
		@include media(md) {

		}
		@include media(lg) {
		}
	}
}