@import "../mixins";

.productTile {
	$root: &;

	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	margin-top: 10px;
	width: 100%;

	@include media(xs) {
	}
	@include media(sm) {

	}
	@include media(md) {

	}
	@include media(lg) {

	}

	&__product {
		margin-bottom: 30px;
		padding: 10px;
		flex: 1 0 100%;
		max-width: 500px;

		@include media(xs) {
			margin-bottom: 0;
			flex: 1 0 300px;
		}
		@include media(sm) {
			flex: 1 0 300px;
			padding: 10px;
		}
		@include media(md) {
			flex: 1 0 320px;
		}
	}

	&__productInner {
		border-radius: 6px;
		overflow: hidden;
		box-shadow: 0 12px 70px -10px rgba(49, 61, 83, 0.24);
		background-color: #FFFFFF;
	}

	&__imageWrap {
		//height: 290px;
		padding-top: 100%;
		width: 100%;
		position: relative;
		z-index: 10;

		@include media(xs) {
		}
		@include media(sm) {

		}
		@include media(md) {

		}
		@include media(lg) {

		}
	}

	&__imageScreen {
		display: flex;
		align-items: center;
		overflow: hidden;
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
	}

	&__imageArrows {
	  position: absolute;
		top: 100%;
		right: 22px;
		transform: translateY(-50%);
		display: flex;
		background-color: #FFFFFF;
		box-shadow: 0 0 15px -2px rgba(57, 61, 149, 0.2);
		border-radius: 4px;
	}

	#{$root}_disable_left {
		#{$root}__arrow_dir_left {
			opacity: 0.5;
		}
	}

	#{$root}_disable_right {
		#{$root}__arrow_dir_right {
			opacity: 0.5;
		}
	}
	
	&__imagePrice {
		position: absolute;
	  left: 22px;
		top: 100%;
		border-radius: 4px;
		padding: 6px 12px;
		background-color: #FFFFFF;
		box-shadow: 0 0 15px -2px rgba(57, 61, 149, 0.2);
		transform: translateY(-50%);
		font-family: 'Gotham Pro', sans-serif;
		font-size: 16px;
		font-weight: bold;
		color: #000000;
	}

	& &__arrow {
		flex-grow: 1;

	  &_dir_left {
		  min-width: 30px;
			border: none;
		  background-color: transparent;
		  transition: opacity 200ms ease-out;
	  }

		&_dir_right {
			min-width: 30px;
			border: none;
			background-color: transparent;
			transform-origin: center;
			transition: opacity 200ms ease-out;
			transform: rotate(180deg);
		}
	}

	&__image {
		width: 100%;
	}

	&__info {
		padding: 25px 30px;
		position: relative;
		z-index: 9;
	}

	&__infoTitle {
		font-family: 'Gotham Pro', sans-serif;
		font-size: 18px;
		font-weight: bold;
		line-height: 1.33;
		margin-bottom: 10px;
	}

	&__infoDescription {
		margin-bottom: 4px;
	}

	&__infoLabel {
		font-family: 'Gotham Pro', sans-serif;
		font-size: 14px;
		font-weight: 500;
	}

	&__infoValue {
		font-family: 'Gotham Pro', sans-serif;
		font-size: 14px;
		font-weight: 300;
	}

	&__footer {
		border-top: 1px solid #F6F3EE;
		margin-top: 20px;
		padding-top: 20px;
	}

	&_theme {
	  &_fullScreen {
			padding: 0;
		  margin: 0;
			position: relative;
		  z-index: 10;

			#{$root}__product {
				padding: 0;
				width: 100%;
				flex: 1 0 100%;
				max-width: none;
			}

		  #{$root}__productInner {
			  border-radius: 10px;
		  }

		  #{$root}__imageWrap {
			  height: auto;
			  display: flex;
		  }
	  }
	}
}