.loader {
	display: flex;
	justify-content: center;
	align-items: center;

	&__icon {
		height: 50px;
		width: 50px;
	  transform-origin: center;
		animation: loader linear 1700ms infinite;
	}
}

@keyframes loader {
	0% {
		transform: rotateZ(0deg);
	}
	100% {
		transform: rotateZ(360deg);
	}
}