@import "../mixins";

.iconText {
	$root: &;

	display: flex;
	align-items: center;

	&__icon {
		display: flex;
		align-items: center;
	}

	&__text {
		padding-left: 8px;
		font-family: 'Gotham Pro', sans-serif;
		font-size: 14px;
		font-weight: 500;
		color: #313131;
	}

	&_theme {
		&_header-phone {
			#{$root}__text {
				display: none;
				@include media(lg) {
					display: inline-block;
				}
			}
		}

		&_basket {

			#{$root}__icon {

			}

			#{$root}__text {
				text-transform: uppercase;
				font-family: 'Gotham Pro', sans-serif;
				font-size: 16px;
				font-weight: 300;
				letter-spacing: 1px;
				color: #313131;
				display: none;
				@include media(md) {
					display: inline-block;
				}
			}
		}

		&_filter {
			height: 100%;
			display: flex;
			align-items: center;
			padding: 10px;
			background-color: transparent;
			border: none;
			outline: none;
			cursor: pointer;

			&:hover {
				#{$root}__text {
					color: #676767;
					text-decoration: underline;
				}
			}

			#{$root}__text {
				font-family: 'Gotham Pro', sans-serif;
				font-size: 16px;
				color: #000000;
				transition: color 200ms ease-out;
				padding-left: 15px;
			}
		}

		&_addToBasket {
			cursor: pointer;
			display: inline-flex;

			&:hover {
				#{$root}__text {
					text-decoration: underline;
				}
			}

			.basket {
				background-color: #bdb4f0;
			}
		}
	}
}
