.checkbox {
	$root: &;

	height: 100%;

	&__label {
		font-family: 'Gotham Pro', sans-serif;
		font-size: 16px;
		font-weight: 300;
		color: #313131;
		cursor: pointer;
		padding: 10px;
		height: 100%;
		display: flex;
		align-items: center;
	}

	&__input {
		position: absolute;
		opacity: 0;
		height: 0;
		width: 0;

		&:checked {
			+ #{$root}__icon {
				background-color: #F8F8F8;

				#{$root}__iconInner {
					transform: scale(1);
					opacity: 1;
				}
			}
		}
	}

	&__icon {
		display: inline-flex;
		justify-content: center;
		align-items: center;
		height: 20px;
		width: 20px;
		background-color: #FFF;
		border: 1px solid #E9E9E9;
		border-radius: 5px;
		margin-left: 15px;
		transition: background-color 200ms ease-out;
	}

	&__iconInner {
		transform: scale(0.8);
		opacity: 0;
		transition: transform 200ms ease-out, opacity 200ms ease-out;
		display: flex;
		align-items: center;
	}

	&_theme {
	  &_listItem {
	    display: flex;
		  align-items: center;
		  width: 100%;
		  height: 42px;

		  #{$root}__label {
			  width: 100%;
		  }

		  #{$root}__labelText {
				flex-grow: 1;
			  white-space: nowrap;
		  }
	  }
	}
}