@keyframes appearanceChar {
	0% {
		transform: translateY(10px) rotate(10deg);
		opacity: 0;
	}
}

@keyframes appearFromTop {
	0% {
		margin-top: -50px;
		opacity: 0;
	}
	100% {
		opacity: 1;
		margin-top: 0;
	}
}

@keyframes appearMarginFromTop {
	0% {
		margin-top: -50px;
		opacity: 0;
	}
	100% {
		margin-top: 0;
		opacity: 1;
	}
}


@keyframes appearanceFromRight {
	0% {
		transform: translateX(50px);
		opacity: 0;
	}
	100% {
		opacity: 1;
		transform: translateX(0);
	}
}

@keyframes appearanceFromLeft {
	0% {
		transform: translateX(-50px);
		opacity: 0;
	}
	100% {
		opacity: 1;
		transform: translateX(0);
	}
}

@keyframes appearVerticalLineFromCenter {
	0% {
		bottom: 50%;
		top: 50%;
	}
	100% {
		bottom: 0;
		top: 0;
	}
}