.mobile-menu {
	background: #fff;
	width: 100%;
	padding-bottom: 20px;
	&__list {
		list-style: none;
		margin: 0;
		padding: 0;
		margin-bottom: 20px;
	}
	&__list-item {

	}
	&__list-link {
		display: block;
		line-height: 1.33;
    	color: #313131;
		text: {
			align: center;
		}
		font: {
			size: 18px;
			family: 'Gotham Pro', sans-serif;
		}
		padding: .6em 1em;
		&--active, &:hover {
			background: #6F68FC;
			color: #fff;
		}
	}
	&__icons {
		display: flex;
		justify-content: center;
	}
	&__icons-item {
		padding: 0 2px;
	}
}