@import "../mixins";

.main {
	min-height: 2000px;
	position: relative;
	z-index: 50;
	overflow: hidden;

	&:before, &:after {
		height: 500px;
		@include media(sm) {
			height: 700px;
		}
		@include media(md) {
			height: 1000px;
		}
		@include media(lg) {
			height: 1300px;
		}
	}

	&:before {
		content: '';
		top: 0;
		left: 0;
		position: absolute;
		background: url('../../assets/images/backgrounds/background-waves-x2.png') left bottom no-repeat;
		background-size: cover;
		width: 100%;
		z-index: -1;
	}

	&:after {
		content: '';
		background: url('../../assets/svg/backgrounds/background-lines.svg') center center no-repeat;
		background-size: 60% 1300px;
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		width: 100%;
		z-index: -1;
	}
}