@import "../../../styles/mixins";

.footer {
	background-color: #313D53;
	width: 100%;
	padding: 0;

	@include media(sm) {
		padding: 30px 0;
	}
	@include media(md) {
	}
	@include media(lg) {

	}

	&__container {
		padding: 20px;

		@include media(sm) {
			padding: 0 80px;
		}
		@include media(md) {

		}
		@include media(lg) {
			padding: 0 120px;
		}
	}

	&__grid {
		display: flex;
		flex-wrap: wrap;
		margin: 0 -10px;
		justify-content: space-between;
		align-items: center;

		@include media(sm) {
			justify-content: space-between;
		}
	}

	&__leftGroup, &__centerGroup, &__rightGroup {
		padding: 0 10px;
		height: 100%;
		display: flex;
		align-items: center;
	}

	&__leftGroup {
		justify-content: flex-start;
		order: 2;
		@include media(sm) {
			order: 1;
		}
	}

	&__centerGroup {
		justify-content: center;
		flex-direction: column;
		flex-grow: 1;
		order: 1;
		margin-bottom: 25px;
		width: 100%;
		@include media(sm) {
			width: auto;
			order: 2;
			margin-bottom: 0;
		}
	}

	&__rightGroup {
		justify-content: flex-end;
		order: 3;
		@include media(sm) {
			order: 3;
		}
	}

	&__iconText {
	  .iconText__text {
		  color: #fff;
	  }
	}
	
	&__thinText {
		opacity: 0.5;
		font-family: 'Gotham Pro', sans-serif;
		font-size: 14px;
		font-weight: 300;
		color: #ffffff;
		margin: 16px 0 20px 0;
		line-height: 1.5;
	}
}