@import "../../../styles/mixins";

.header {
	//background-color: #F7F7F7;
	position: relative;
	width: 100%;
	z-index: 100;
	background-color: #FFF;
	box-shadow: 0 0 10px 0 rgba(214, 214, 214, 0.5);

	&__mobile-menu {
		position: absolute;
		width: 100%;
		bottom: 0;
		transform: translateY(100%);
		transition-property: opacity, visibility;
		transition-timing-function: ease;
		transition-duration: .3s;
		opacity: 0;
		visibility: hidden;
		display: block;
		&--active {
			opacity: 1;
			visibility: visible;
		}
		@include media(md) {
			display: none;
		}
	}
}

.navbar {
	display: flex;
	height: 86px;
	animation: appearMarginFromTop 500ms ease-out 1 both;

	&__inner {
		display: flex;
		justify-content: space-between;
		align-items: center;
		height: auto;
		width: 100%;
		overflow-x: hidden;
	}

	&__logo {
		
	}
	
	&__list {
		list-style: none;
		display: none;
		@include media(md) {
			display: flex;
		}
	}
	&__listItem {
		margin-right: 30px;
		&:last-child {
			margin-right: 0;
		}
	}
	&__listLink {
		display: block;
		font-family: 'Gotham Pro', sans-serif;
		font-size: 16px;
		font-weight: 300;
		letter-spacing: 1px;
		color: #313131;
		text-transform: uppercase;
		text-decoration: none;
		cursor: pointer;
		padding: 6px 0;
		position: relative;

		&:visited {
			color: #313131;
		}

		&:hover {
			&:after {
				width: 100%;
			}
		}

		&:after {
			content: '';
			height: 2px;
			width: 0;
			background-color: #BDB4F0;
			position: absolute;
			left: 50%;
			bottom: 0;
			transform: translateX(-50%);
			transition: width 200ms ease-out;
		}
	}

	&__main, &__right, &__contacts {
		display: flex;
		align-items: center;
		height: 100%;
	}

	&__main {
		padding-left: 0;
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
		@include media(md) {
			padding-left: 100px;
			position: static;
			transform: none;
		}
	}

	&__contacts {
		display: none;
		@include media(xs) {
			display: flex;
		}
	}
	 
	&__mobile-button {
		display: block;
		padding: 0 15px;
		button {
			border: none;
			background: none;
			outline: none;
			cursor: pointer;
			display: flex;
			align-items: center;
			justify-content: center;
		}
		@include media(md) {
			display: none;
		}
	}

	&__groupItem {
		margin-right: 10px;
		@include media(md) {
			margin-right: 30px;
		}
		&:last-child {
			// margin-right: 0;
		}
	}

	&__phoneWrap {
		display: flex;
		align-items: center;
	}

	&__socialIcon {
		cursor: pointer;
		display: flex;
		align-items: center;
		margin: 0 5px;
		transition: opacity 200ms ease-out;
	}
}
