.button {
	background-color: transparent;
	border: none;
	outline: none;
	cursor: pointer;
	transition: opacity 200ms ease-out;

	&:disabled {
		opacity: 0.2;
		background-color: #716AFC;

	  &:hover {
	    background-color: #716AFC;
	  }
	}
}

.button_theme_primary {
	padding: 18px 50px;
	border-radius: 30px;
	text-transform: uppercase;
	font-family: 'Gotham Pro', sans-serif;
	font-size: 16px;
	font-weight: 500;
	letter-spacing: 1px;
	text-align: right;
	background-color: #6F68FC;
	color: #ffffff;
	box-shadow: 0 8px 40px -12px rgba(73, 43, 194, 0.4);
	transition: background-color 200ms ease-out, box-shadow 200ms ease-out, text-shadow 100ms ease-out;

	&:hover {
		background-color: #5c56d0;
		box-shadow: 0 8px 40px -12px rgb(78, 45, 212);
	}

	&:active {
		background-color: #4b46a9;
		text-shadow: 0 0 4px #FFF;
	}

	&:focus {
		background-color: #5e57df;
	}
}