.tags {
	width: 100%;
	display: flex;
	flex-wrap: wrap;
	margin-left: -8px;
	margin-right: -8px;

	&__tag {
		padding: 8px 32px 8px 16px;
		font-family: 'Gotham Pro', sans-serif;
		font-size: 14px;
		font-weight: 300;
		color: #313131;
		display: flex;
		align-items: center;
		border-radius: 20px;
		box-shadow: 0 4px 12px 0 rgba(174, 172, 205, 0.15);
		background-color: #ffffff;
		position: relative;
		cursor: pointer;
		transition: box-shadow 200ms ease-out;
		margin: 8px;

		path {
			transition: fill 200ms ease-out;
		}

		&:hover {
			box-shadow: 0 4px 12px 0 rgba(227, 52, 72, 0.22);

		  path {
			  fill: red;
		  }
		}
	}

	&__icon {
		position: absolute;
		right: 10px;
		top: 6px;
	}
}