.dropdown {
	$root: &;

	height: 100%;
	width: 100%;
	position: relative;

	&__button {
		display: flex;
		justify-content: center;
		align-items: center;
		padding: 0 20px;
		background-color: transparent;
		border: none;
		outline: none;
		cursor: pointer;
		height: 100%;
		width: 100%;
		font-family: 'Gotham Pro', sans-serif;
		font-size: 16px;
		font-weight: 300;
		color: #313131;
		transition: background-color 150ms ease-out;
	}

	&__buttonText {
	  padding-right: 15px;
	}

	&__icon {
		display: inline-flex;
		justify-content: center;
		align-items: center;
		transform-origin: center;
	  transition: transform 200ms ease-out;
	}

	&__panel {
		position: absolute;
		top: 100%;
		left: 50%;
		transform: translateX(-50%);
		background-color: #FFF;
		border-radius: 6px;
		//min-width: 200px;
		//min-height: 200px;
		box-shadow: 0 12px 70px -10px rgba(49, 61, 83, 0.24);
		display: none;
		margin-top: 10px;
		z-index: 100;

		&:after {
			content: '';
			position: absolute;
			bottom: 100%;
			left: 50%;
			margin-left: -5px;
			border-width: 8px;
			border-style: solid;
			border-color: transparent transparent #FFF transparent;
		}
	}

	&_open {
		#{$root}__button {
			background-color: #F8F8F8;
		}

		#{$root}__icon {
			transform: rotateZ(180deg);
		}

		#{$root}__panel {
			display: block;
		}
	}
}