.input {
	$root: &;

	&__label {
		display: flex;
		cursor: pointer;
	}

	&__labelText {
		padding: 15px;
		font-family: 'Gotham Pro', sans-serif;
		font-size: 16px;
		font-weight: 300;
		color: #313131;
	}

	&__field {
		width: 82px;
		height: 42px;
		padding: 10px 12px;
		border: 1px solid #E9E9E9;
		border-radius: 10px;
		outline: none;
		font-family: 'Gotham Pro', sans-serif;
		font-size: 16px;
		font-weight: 500;
		text-align: center;
		color: #313131;
	}

	&_theme {
	  &_listItem {
		  #{$root}__label {
		    display: flex;
		  }

	    #{$root}__labelText {
		    flex-grow: 1;
	    }

		  #{$root}__field {
		    height: 38px;
		  }
	  }
	}
}